var tippyInstance;

// Configuraes padro
tippy.setDefaultProps({
  allowHTML: true, // Habilita as tags HTML no contedo
  maxWidth: 250,
  theme: "lp-dark",
});

// Inicializa a instncia do Tippy.js
function initTippy() {
  tippyInstance = tippy("[data-tippy-content]");
}
initTippy();
